<template>
    <div class="page">
        <div class="title">
            全部
        </div>
        <div class="container">
            <div class="title">
                基本信息
            </div>
            <div v-if="!loading&&detail==''">
                <el-alert
                    title="暂无数据"
                    type="info"
                    center
                    show-icon>
                </el-alert>
            </div>
            <spin :loading="loading"/>
            <div v-if="detail!==''">
                <div class="item">
                    <img src="@/assets/image/logo/logo100.png" alt="">
                    <span class="description">
                        <div>老师：{{detail.teacherNickname}}</div>
                        <div>课程：{{detail.courseName}}</div>
                        <div>代码：{{detail.courseCode}}</div>
                        <div>学分：{{detail.courseCredit}}</div>
                    </span>
                </div>
            </div>
            <div class="title">
                基础介绍
            </div>
            <div class="introContent">
                {{detail.courseIntroduction}}
            </div>
            <div class="title">
                班课操作
            </div>
            <div class="introContent">
                <el-row>
                    <span v-if="isLock==false">
                        <el-button type="warning"  @click="lockCourse()">锁定课程</el-button>
                        <div>锁定完状态可以恢复</div>
                    </span>
                    <span v-if="isLock==true">
                        <el-button type="success" :disabled="isOver" @click="recoverCourse()">恢复课程</el-button>
                        <div>锁定完状态可以恢复</div>
                    </span>
                    <span>
                        <el-button type="danger" @click="overCourse()">完结课程</el-button>
                        <div>结束了就不可以恢复</div>
                    </span>
                    <span>
                        <el-button type="info" :disabled="!isOver" @click="delCourse()">删除课程</el-button>
                        <div>只有完结课程才可以删除</div>
                    </span>
                </el-row>
            </div>
        </div>
    </div>
</template>

<script>
import spin from '@/components/spin.vue'
export default {
    props: {

    },
    data() {
        return {
            detail:"",
            loading: true,
            isOver: false,
            isLock: false
        };
    },
    computed: {

    },
    created() {
        this.$http.get(`/teaching/teacher/course/detail/${this.$route.params.courseId}`
        ).then((res) => { 
            if(res.data.code == "0"){
                this.detail = res.data.data
            }else if (res.data.code == "1") {
                this.$router.push('/login'); 
            }else{
                this.$message({
                    message: res.data.msg,
                    type: 'error'
                });
            }
            this.loading=false
        })
        .catch(function (error) {
            console.log(error)
        })
    },
    mounted() {

    },
    watch: {

    },
    methods: {
        lockCourse(){
            this.$http.get(`/teaching/teacher/course/lock/${this.$route.params.courseId}`
            ).then((res) => { 
                if(res.data.code == "0"){
                    this.$message({
                        message: res.data.msg,
                        type: 'success'
                    });
                    this.isLock=true
                }else if (res.data.code == "1") {
                    this.$router.push('/login'); 
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error'
                    });
                }
            })
            .catch(function (error) {
                console.log(error)
            })
        },
        recoverCourse(){
            this.$http.get(`/teaching/teacher/course/unlock/${this.$route.params.courseId}`
            ).then((res) => { 
                if(res.data.code == "0"){
                    this.$message({
                        message: res.data.msg,
                        type: 'success'
                    });
                    this.isLock=false
                }else if (res.data.code == "1") {
                    this.$router.push('/login'); 
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error'
                    });
                }
            })
            .catch(function (error) {
                console.log(error)
            })
        },
        overCourse(){
            this.$http.get(`/teaching/teacher/course/end/${this.$route.params.courseId}`
            ).then((res) => { 
                if(res.data.code == "0"){
                    this.$message({
                        message: res.data.msg,
                        type: 'success'
                    });
                    this.isOver=true
                }else if (res.data.code == "1") {
                    this.$router.push('/login'); 
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error'
                    });
                }
            })
            .catch(function (error) {
                console.log(error)
            })
        },
        delCourse(){
            this.$http.get(`/teaching/teacher/course/invalid/${this.$route.params.courseId}`
            ).then((res) => { 
                if(res.data.code == "0"){
                    this.$message({
                        message: res.data.msg,
                        type: 'success'
                    });
                }else if (res.data.code == "1") {
                    this.$router.push('/login'); 
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error'
                    });
                }
            })
            .catch(function (error) {
                console.log(error)
            })
        }
    },
    components: {
        spin
    },
};
</script>

<style scoped lang="scss">
.page{
    padding:0 20px;
    .title{
        padding:10px;
        position: relative;
        border-bottom: 1px solid #E9E9E9;
        font-weight: bold;
    }
    .container{
        padding:1em;
        .item{
            padding:1em;
            img{
                width: 100px;
                height: 100px;
            }
            .description{
                display: inline-block;
                line-height: 1.8em;
                margin-left: 1em;
            }
        }
        .introContent{
            padding:1em;
            span{
                padding: 10px;
                text-align: center;
                display: inline-block;
                div{
                    padding: 7px;
                    font-size: 12px;
                    color:#E02020;
                }
            }
        }
    }
}
</style>



